:root {
  --bg1: #B3B3B3;
  --bg2: #B9BAA3;
  --bg3: #746F72;
  --bg4: #F7EBEC;
  --bg5: #D6D5C9;
}

h1 {
  padding-top: 0px;
  margin-top: 0px;
  padding-left: 5px;
}

h2 {
  padding-left: 50px;
}

body {
  user-select: none;
}

a {
  color: var(--bg4);
}

.svg{
  fill: red;
}

input[type=range] {
  -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
  width: 100%; /* Specific width is required for Firefox. */
  background: transparent; /* Otherwise white in Chrome */
}

input[type="range"]::selection{
  border-color: red;
}

a:visited {
  color: var(--bg5);
}

.ArborEmbed {
}

.Controller {
  position: relative;
  display: flex;
  flex-direction: row;
  height: 100px;
}

.MainPage {
  font-family: 'YZ-Standard';
  background-color: var(--bg3);
  min-height: 100vh;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--bg1);
}

.MediaContentContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-height: 300px;
  margin-right: 10px;
}

.Selector {
  display: flex;
  flex-direction: row;
}

.Controller {
  color: var(--bg3);
  background-color: var(--bg1);
}

.MediaContent {
  width: 100%;
  background-color: var(--bg1);
  display: flex;
  flex-direction: column;
  color: var(--bg3);
}

.MediaSelector {
  font-size: 0.7em;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 10px;
  display: flex;
  flex-direction: row;
}

.MediaSelector:hover {
  background-color: var(--bg3);
  color: var(--bg1);
}

.MediaSelector:active {
  background-color: var(--bg1);
  color: var(--bg3);
}

.ProjectTitle {
  font-size: 1.2em;
  margin-left: 10px;
  margin-bottom: 20px;
}

.LinkBox {
  padding-left: 100px;
}

.TitleIcon {
  padding: 20px 10px 10px 10px;
}

.display-none {
  visibility: hidden;
}

.navigation-selector:hover {
  color: var(--bg4);
}

